/*========================
    animation CSS start
==========================*/
@keyframes moveObject1 {
    0% {
        top: 0;
        left: 0;
        transform: scale(0.5) rotate(45deg);
    }
    30% {
        top: 0;
        left: calc(100% - 200px);
        transform: scale(0.5) rotate(-45deg);
    }
    60% {
        top: calc(100% - 200px);
        left: 50%;
        transform: scale(1) rotate(0) translateX(-50%);
    }
    100% {
        top: 0;
        left: 0;
        transform: scale(0.5) rotate(45deg);
    }
}

@keyframes moveObject2 {
    0% {
        top: 0;
        left: calc(100% - 200px);
        transform: scale(0.5) rotate(-45deg);
    }
    30% {
        top: calc(100% - 200px);
        left: 50%;
        transform: scale(1) rotate(0) translateX(-50%);
    }
    60% {
        top: 0;
        left: 0;
        transform: scale(0.5) rotate(45deg);
    }

    100% {
        top: 0;
        left: calc(100% - 200px);
        transform: scale(0.5) rotate(-45deg);
    }
}

@keyframes moveObject3 {
    0% {
        top: calc(100% - 200px);
        left: 50%;
        transform: scale(1) rotate(0) translateX(-50%);
    }
    30% {
        top: 0;
        left: 0;
        transform: scale(0.5) rotate(45deg);
    }
    60% {
        top: 0;
        left: calc(100% - 200px);
        transform: scale(0.5) rotate(-45deg);
    }
    100% {
        left: 50%;
        top: calc(100% - 200px);
        transform: scale(1) rotate(0) translateX(-50%);
    }
}
